<template>
<div class="autherinfo">
  <div class="authersummay">
    <div class="topbar">‰ΩúËÄÖ</div>
    <router-link :to="{
          name:'user_info',
          params:{
            name:userinfo.loginname
          }
          }">
      <img :src="userinfo.avatar_url" alt="">
    </router-link>
  </div>
  <div class="recent_topics">
    <div class="topbar">‰ΩúËÄÖÊúÄËøë‰∏ªÈ¢ò</div>
    <ul>
      <li v-for="list in topiclimitby5">
        <router-link :to="{
        name:'post_content',
        params:{
          id:list.id,
          name:list.author.loginname
        }
        }">
          {{list.title}}
        </router-link>
      </li>
    </ul>
  </div>
  <div class="recent_replies">
    <div class="topbar">‰ΩúËÄÖÊúÄËøëÂõûÂ§ç</div>
    <ul>
      <li v-for="list in replylimitby5">
        <router-link :to="{
        name:'post_content',
        params:{
          id:list.id,
          name:list.author.loginname
        }
        }">
          {{list.title}}
        </router-link>
      </li>
    </ul>
  </div>
</div>
</template>

<script>
    export default {
        name: "SlideBar",
      data(){
          return {
            isLoading:false,
            userinfo:{}
          }
      },
      methods:{
        getData(){
          // https://cnodejs.org/api/v1/user/${this.$route.params.name}
          // http://mock.hunger-valley.com/cnode/api/v1/user/${this.$route.params.name}
          this.$http.get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
            .then(res=>{
              this.isLoading = false; //Âä†ËΩΩÊàêÂäüÔºåÂéªÈô§Âä®Áîª
              this.userinfo = res.data.data;
            })
            .catch(function (err) {
              //Â§ÑÁêÜËøîÂõûÂ§±Ë¥•ÂêéÁöÑÈóÆÈ¢ò
              console.log(err)
            })
        }
      },
      computed:{
        topiclimitby5(){
          if(this.userinfo.recent_topics){
            return this.userinfo.recent_topics.slice(0,5);
          }
        },
        replylimitby5(){
          if(this.userinfo.recent_replies){
            return this.userinfo.recent_replies.slice(0,5);
          }
        }
      },
      beforeMount(){
        this.isLoading = true;//Âä†ËΩΩÊàêÂäü‰πãÂâçÊòæÁ§∫Âä†ËΩΩÂä®Áîª
        this.getData();//Âú®È°µÈù¢Âä†ËΩΩ‰πãÂâçËé∑ÂèñÊï∞ÊçÆ
      }
    }
</script>

<style scoped>
  .authersummay, .recent_replies, .recent_topics {
    background-color: #fff;
  }
  .autherinfo {
    width: 328px;
    float: right;
    margin-top: 0;
  }
  li{
    padding: 3px 0 ;
  }
  .recent_replies ul, .recent_topics ul {
    margin-top: 0px;
    margin-bottom: 0px;
    list-style: none;
    padding-left: 14px;
  }
  ul a {
    font-size: 12px;
    text-decoration: none;
    color: #778087;
  }
  .topbar {
    padding: 10px;
    background-color: #f6f6f6;
    height: 16px;
    font-size: 12px;
    margin-top: 10px;
  }
  img {
