<template>
<div class="pagination">
  <button @click="changeBtn">È¶ñÈ°µ</button>
  <button @click="changeBtn">‰∏ä‰∏ÄÈ°µ</button>
  <button v-if="jduge" class="pagebtn">......</button>
  <button v-for="btn in pagebtns"
          @click="changeBtn(btn)"
  :class="[{currentPage:btn == currentPage},'pagebtn']">
    {{btn}}
  </button>
  <button @click="changeBtn">‰∏ã‰∏ÄÈ°µ</button>
</div>
</template>

<script>
  import $ from 'jquery'

    export default {
        name: "Pagination",
      data(){
          return {
            pagebtns:[1,2,3,4,5,'......'],
            currentPage:1,
            jduge:false
          }
      },
      methods:{
        changeBtn(page){
          //ÁÇπÂáª‰∏ä‰∏ÄÈ°µÔºå‰∏ã‰∏ÄÈ°µ,È¶ñÈ°µ
          if(typeof page != 'number'){
            switch(page.target.innerText){
              case '‰∏ä‰∏ÄÈ°µ':
                $('button.currentPage').prev().click();
                break;
              case '‰∏ã‰∏ÄÈ°µ':
                $('button.currentPage').next().click();
                break;
              case 'È¶ñÈ°µ':
                this.pagebtns = [1,2,3,4,5,'......'];
                this.changeBtn(1);
                break;
              default:
                break;
            }
            return;
          }
          this.currentPage = page;
          if(page>4){
            this.jduge = true;
          }else{
            this.jduge = false;
          }
          if(page == this.pagebtns[4] ){
            this.pagebtns.shift();//ÁßªÈô§Á¨¨‰∏Ä‰∏™ÂÖÉÁ¥†
            this.pagebtns.splice(4,0,this.pagebtns[3]+1);//Ê∑ªÂä†ÊúÄÂêé‰∏Ä‰∏™
          }else if(page == this.pagebtns[0] && page !=1){
            //ÂÖàÂú®Á¨¨‰∏Ä‰∏™‰ΩçÁΩÆÂä†‰∏Ä‰∏™
            this.pagebtns.unshift(this.pagebtns[0]-1);
              //ÁßªÈô§ÊúÄÂêé‰∏Ä‰∏™Êï∞Â≠ó
            this.pagebtns.splice(5,1);
          }
        //   debugger
          this.$emit('handleList',this.currentPage);
        }
      }
    }
</script>

<style scoped>
  .pagination {
    margin-top: 5px;
    margin-bottom: 20px;
    background-color: white;
    padding: 6px 20px;
    border-radius: 5px;
    /*box-shadow: 0px 2px 9px #888888;*/
    border: 1px solid #888888;
  }

  button {
    background-color: #fff;
    border: 1px solid #ddd;
    color: #778087;
    border-radius: 3px;
    outline: none;
    height: 21px;
    cursor: pointer;
    padding: 0 2px;
    width: 55px;
    height: 29px;
  }

  .pagebtn {
    position: relative;
    bottom: 1px;
    width: 40px;
    margin: 0 4px;
  }

  .currentPage {
    color: white;
    background-color: #1f1b1b;

  }
</style>
